


























import { computed, defineComponent, onMounted, ref } from '@vue/composition-api'
// import { useStore } from 'vuex'

export default defineComponent({
    name: 'GeneralCard',
    props:{
        width:{
            type: Number,
            default: 500,
        },
        title:{
            type: String,
            default: ""
        },
        titleIcon:{
            type: String,
            default: ""
        },
        loading:{
            type: Boolean,
            default: false,
        }
        
    },
    setup(props, { emit, root, slots }){
        const isCardActionEmpty = computed( () => !slots["action"] )

        return {
            isCardActionEmpty,
        }
    }
});
